import React from 'react'
import ReactDOM from 'react-dom'
import {
    BrowserRouter as Router,
    Route,
    Link,
    Switch,
} from 'react-router-dom'
import './index.css'
import First from './First'
import Second from './Second'
import Third from './Third'
import Fourth from './Fourth'
import Fifth from './Fifth'
import Sixth from './Sixth'
import Seventh from './Seventh'
import Eighth from './Eighth'
import Ninth from './Ninth'
import Tenth from './Tenth'
import Eleventh from './Eleventh'
import Twelfth from './Twelfth'


const Index = () => (
    <Router>
        <div>
            <h2>React-Router</h2>
            <ul className="examples">
                <li><Link to='/'></Link></li>
                <li><Link to='/First'></Link></li>
                <li><Link to='/Second'>URL</Link></li>
                <li><Link to='/Third'></Link></li>
                <li><Link to='/Fourth'></Link></li>
                <li><Link to='/Fifth'></Link></li>
                <li><Link to='/Sixth'>404</Link></li>
                <li><Link to='/Seventh'></Link></li>
                <li><Link to='/Eighth'></Link></li>
                <li><Link to='/Ninth'></Link></li>
                <li><Link to='/Tenth'></Link></li>
                <li><Link to='/Eleventh'></Link></li>
                <li><Link to='/Twelfth'></Link></li>
            </ul>

            <Switch>
                <Route exact path='/'/>
                <Route path='/First' component={First}/>
                <Route path='/Second' component={Second}/>
                <Route path='/Third' component={Third}/>
                <Route path='/Fourth' component={Fourth}/>
                <Route path='/Fifth' component={Fifth}/>
                <Route path='/Sixth' component={Sixth}/>
                <Route path='/Seventh' component={Seventh}/>
                <Route path='/Eighth' component={Eighth}/>
                <Route path='/Ninth' component={Ninth}/>
                <Route path='/Tenth' component={Tenth}/>
                <Route path='/Eleventh' component={Eleventh}/>
                <Route path='/Twelfth' component={Twelfth}/>
                <Route component={NoMatch}/>
            </Switch>
        </div>
    </Router>
) 

const NoMatch = ({ location }) => (
  <div>
    <h3> <code>{location.pathname}</code></h3>
  </div>
)

ReactDOM.render(
    <Index />, 
document.getElementById('root'));




