import React, {Component} from 'react'
import {
  BrowserRouter as Router,
  Route,
  Link,
  Prompt
} from 'react-router-dom'


export default ({match}) => (
    <Router>
        <div>
            <ul>
                <li><Link to={`${match.url}/`}></Link></li>
                <li><Link to={`${match.url}/one`}> 1</Link></li>
                <li><Link to={`${match.url}/two`} > 2</Link></li>
            </ul>
            <Route path={`${match.url}/`} exact component={Form}/>
            <Route path={`${match.url}/one`} render={() => <h3> 1</h3>}/>
            <Route path={`${match.url}/two`} render={() => <h3> 2</h3>}/>
        </div>
    </Router>
)

class Form extends Component {
    state = {
        isBlocking: false
    }

    render() {
        const { isBlocking } = this.state
        return (
            <form
                onSubmit = {
                    e => {
                        e.preventDefault()
                        e.target.reset()
                        this.setState({
                            isBlocking: false
                        })
                    }
                }
            >
            <Prompt
                when={isBlocking}
                message={
                    location => (
                        ` ${location.pathname}`
                    )
                }
            />
                <p>
                    ? {isBlocking ? '' : ''}
                </p>
                
                <p>
                    <input
                        size="50"
                        placeholder=""
                        onChange={e => {
                            this.setState({
                                isBlocking: e.target.value.length > 0
                            })
                        } }
                        />
                </p>

                <p>
                    <button></button>
                </p>
            </form>
        )
    }
}


