import React from 'react'
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'


// 
// React  <Route> 
// 


////////////////////////////////////////////////////////////
// route

/*const Main = () => <h2></h2>*/

const Redbull = () => <h2></h2>

const Snacks = ({ routes, match }) => (
  <div>
    <h2></h2>
    <ul>
      <li><Link to={`${match.url}/snacks/spicy`}></Link></li>
      <li><Link to={`${match.url}/snacks/chips`}></Link></li>
    </ul>

    {routes.map((route, i) => (
      <RouteWithSubRoutes key={i} {...route} match={match}/>
    ))}
  </div>
)

const Spicy = () => <h3></h3>
const Chips = () => <h3></h3>

////////////////////////////////////////////////////////////
// 

const routes = [
  { path: '/redbull',
    component: Redbull
  },
  { path: '/snacks',
    component: Snacks,
    routes: [
      { path: '/snacks/spicy',
        component: Spicy
      },
      { path: '/snacks/chips',
        component: Chips
      }
    ]
  }
]


//  <Route>  <Route>  <RouteWithSubRoutes>
// 
const RouteWithSubRoutes = (route) => (
  <Route path={route.match.url + route.path} render={props => (
    // 
    <route.component {...props} routes={route.routes}/>
  )}/>
)

export default ({match}) => (
    <Router>
        <div>
        <ul>
            <li><Link to={`${match.url}/snacks`}></Link></li>
            <li><Link to={`${match.url}/redbull`}></Link></li>
        </ul>

        {routes.map((route, i) => (
            <RouteWithSubRoutes key={i} {...route} match={match}/>
        ))}
        </div>
    </Router>
)


