import React from 'react'
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch
} from 'react-router-dom'

export default ({match}) => (
  <Router>
    <div>
      <ul>
        <li><Link to={`${match.url}/about`}>About Us (static)</Link></li>
        <li><Link to={`${match.url}/company`}>Company (static)</Link></li>
        <li><Link to={`${match.url}/kim`}>Kim (dynamic)</Link></li>
        <li><Link to={`${match.url}/chris`}>Chris (dynamic)</Link></li>
      </ul>

      {/*
          /about/company
          /:user/about
          /about""/:user
          
           route  React Router 
          sidebarsbreadcrumbs /about
          /:user <Switch>  <Route> 
           <Switch> 
      */}
      <Switch>
        <Route path={`${match.url}/about`} component={About}/>
        <Route path={`${match.url}/company`} component={Company}/>
        <Route path={`${match.url}/:user`} component={User}/>
      </Switch>
    </div>
  </Router>
)

const About = () => <h2>About</h2>
const Company = () => <h2>Company</h2>
const User = ({ match }) => (
  <div>
    <h2>User: {match.params.user}</h2>
  </div>
)


