import React from 'react'
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'

//  sidebar 
// URL

const routes = [
    {
        path: '/',
        exact: true,
        sidebar: () => <div>!</div>,
        main: () => <h2></h2>
    },
    {
        path: '/about',
        sidebar: () => <div>!</div>,
        main: () => <h2></h2>
    },
    {   path: '/contact',
        sidebar: () => <div>!</div>,
        main: () => <h2></h2>
    }
]

export default ({match}) => (
    <Router>
        <div style={{ display: 'flex' }}>
        <div style={{
            padding: '10px',
            width: '40%',
            background: '#f0f0f0'
        }}>
            <ul style={{ listStyleType: 'none', padding: 0 }}>
                <li><Link to={`${match.url}/`}></Link></li>
                <li><Link to={`${match.url}/about`}></Link></li>
                <li><Link to={`${match.url}/contact`}></Link></li>
            </ul>

            {routes.map((route, index) => (
            // app <Route>  <Route> 
            //  URL  URL 
            //  sidebar  breadcrumb
            // <Route> 

            <Route
                key={index}
                path={`${match.url}` + route.path}
                exact={route.exact}
                component={route.sidebar}
            />
            ))}
        </div>

        <div style={{ flex: 1, padding: '10px' }}>
            {routes.map((route, index) => (
            //  <Route> 
            // <Route>  component 
            <Route
                key={index}
                path={`${match.url}` + route.path}
                exact={route.exact}
                component={route.main}
            />
            ))}
        </div>
        </div>
    </Router>
)



