import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from 'react-router-dom'

//  URL  context 
// Galary
//  URL 

class ModalSwitch extends React.Component {

  // location <Switch/> 
  //  prop location
  // location statemodal/images/2
  // modal gallery 
  // /images/2 gallery / 
  // Switch
  // /images/2Switch/

  previousLocation = this.props.location

  componentWillUpdate(nextProps) {
    const { location } = this.props
    //  props.location  modal  this.props.location 
    //  previousLocation
    if (
      nextProps.history.action !== 'POP' &&
      (!location.state || !location.state.modal)
    ) {
      this.previousLocation = this.props.location
    }
  }

  render() {

  

    const { location } = this.props
    const { match } = this.props
    const isModal = !!(
      location.state &&
      location.state.modal &&
      this.previousLocation !== location // 
    )
      {console.log('isModal: ' + isModal)}

    return (
      <div>
        <Switch location={isModal ? this.previousLocation : location}>
          <Route exact path={`${match.url}/`} component={Home}/>
          <Route path={`${match.url}/gallery`} component={Gallery}/>
          <Route path={`${match.url}/img/:id`} component={ImageView}/>
        </Switch>
        {/*isModal ? <Route path={`${match.url}/img/:id`} component={Modal} /> : null*/}
        {isModal ?  <Route path={`${match.url}/img/:id`} component={Modal} /> :
           console.log(`${match.url}/img/:id`)}
      </div>
    )
  }
}

const IMAGES = [
  { id: 0, title: '', color: 'DarkOrchid' },
  { id: 1, title: '', color: 'LimeGreen' },
  { id: 2, title: '', color: 'Tomato' },
  { id: 3, title: '#', color: '#789' },
  { id: 4, title: '', color: 'Crimson' }
]

const Thumbnail = ({ color }) =>
  <div style={{
    width: 50,
    height: 50,
    background: color
  }}/>



const Home = ({match}) => (
  <div>
    <Link to={`${match.url}/gallery`}> Galary </Link>
    <h2></h2>
    <ul>
      <li><Link to={`${match.url}/img/2`}></Link></li>
      <li><Link to={`${match.url}/img/4`}></Link></li>
    </ul>
  </div>
)

const Gallery = ({match}) => (
  <div>
    {IMAGES.map(i => (
      <Link
        key={i.id}
        to={{
          pathname: `${match.url}/img/${i.id}`,
          // 
          state: { modal: true }
        }}
      >
        <Thumbnail color={i.color} />
        <p>{i.title}</p>
      </Link>
    ))}
  </div>
)

const Image = ({ color }) =>
  <div style={{
    width: '100%',
    height: 400,
    background: color
  }}></div>

const ImageView = ({ match }) => {
  const image = IMAGES[parseInt(match.params.id, 10)]
  if (!image) {
    return <div></div>
  }

  return (
    <div>
      <h1>{image.title}</h1>
      <Image color={image.color} />
    </div>
  )
}

const Modal = ({ match, history }) => {
  
  const image = IMAGES[parseInt(match.params.id, 10)]
   console.log(image)
  if (!image) {
   
    return null
  }
  const back = (e) => {
    e.stopPropagation()
    history.goBack()
  }
  return (
    <div
      onClick={back}
      style={{
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
        background: 'rgba(0, 0, 0, 0.15)'
      }}
    >
      <div className='modal' style={{
      position: 'absolute',
        background: '#fff',
        top: 25,
        left: '10%',
        right: '10%',
        padding: 15,
        border: '2px solid #444'
      }}>
        <h1>{image.title}</h1>
        <Image color={image.color} />
        <button type='button' onClick={back}>
          
        </button>
      </div>
    </div>
  )
}


export default ({match}) => (
  <Router>
    <Route component={ModalSwitch} {...match}/>
  </Router>
)


